  background: linear-gradient(145deg, #ffc226 0%, #f84119 100%);
}

.search.btn__search_black:hover, .search.btn__search_black:active {
  background: var(--white);
  color: #f84119;
  border: 1px solid #f84119;
}

.search.btn__search_black:hover .icon path {
  stroke: url("#myGradient");
}

.btn__search_white {
  color: var(--white);
  background: linear-gradient(145deg, #ffc226 0%, #f84119 100%);
}

.search.btn__search_white:hover, .search.btn__search_white:active {
  background: var(--white);
  color: #f84119;
  border: 1px solid #f84119;
}

.icon {
  fill: none;
  stroke: var(--white);
}

